
function mapsEqual(mapA, mapB) {
	if(mapA == null || mapB == null) {
		return false;
	}

	if(mapA.size !== mapB.size) {
		return false;
	}

	for(let [key, value] of mapA.entries()) {
		if(mapB.has(key) === false) {
			return false;
		}
		else if(mapB.get(key) !== value) {
			return false;
		}
	}

	return true;
}

export default mapsEqual;



