
function deepClone(obj) {
	return JSON.parse(JSON.stringify(obj));
}
const colorChain = [
	'red',
	'green',
	'blue',
	'purple',
	'yellow',
	'Chartreuse',
	'CornflowerBlue',
	'#B22222',
	'#FF00FF',
	'#DAA520',
	'#008000',
	'#4B0082',
	'#CD5C5C',
	'#4B0082',
	'#7CFC00',
	'#FFA07A',
	'#800000',
	'#3CB371',
	'#7B68EE',
	'#191970',
	'#6B8E23',
	'#FF4500',
	'#DB7093',
	'#FF0000',
];

const configs = {
	default: {
		options: {
			responsive:true,
			title: {
				display:true,
				text:'Responses'
			},
			legend: {
				display:true
			}
		},
		data: {
			labels:[],
			datasets: [{
				data:[],
				backgroundColor:colorChain
			}]
		}
	}
}
configs.bar = deepClone(configs.default);
configs.bar.options.legend.display = false;
configs.bar.options.scales = {
	yAxes: [{
		ticks: {
			beginAtZero: true
		}
	}]
}

function chartConfig(chartType) {
	let c = configs[chartType];
	if(c == null) {
		c = configs.default;
	}

	// deep clone
	return JSON.parse(JSON.stringify(c));
}


export default chartConfig;



