import React from 'react';
import ChartCanvas from './chartCanvas/ChartCanvas';
import ChartType from './chartType/ChartType';
import vwmApi from '../../services/vwmApi';
import './Chart.css';

class Chart extends React.Component {
	constructor(properties) {
		super(properties);

		this.state = {chartType:null, responses:null, error:null, errorDescription:null};
		this.locals = {fetchInProgress:false, fetchTimer:-1, fetchDelay:100, chartCanvasInstance:null};

		this.fetchResponses = this.fetchResponses.bind(this);
		this.newQuestion = this.newQuestion.bind(this);
		this.onResponsesFetched = this.onResponsesFetched.bind(this);
		this.onNewQuestion = this.onNewQuestion.bind(this);
		this.onTypeChosen = this.onTypeChosen.bind(this);
	}

	componentDidMount() {
		this.fetchResponses();
	}

	render() {
		let errorElement = null;
		if(this.state.error) {
			errorElement = <p className='Chart-error'>{this.state.errorDescription}</p>
		}

		const responseCounts = this.state.responses ? vwmApi.getResponseCounts(this.state.responses) : new Map();
		const canvasProps = {responseCounts:responseCounts, chartType:this.state.chartType};

		return (
			<div className='Chart'>
				{errorElement}
				<button className='Chart-btn' onClick={this.newQuestion}>New Question</button>
				<div className='Chart-main-content'>
					<ChartCanvas {...canvasProps}/>
					<ChartType onTypeChosen={this.onTypeChosen}/>
				</div>
			</div>
		);
	}

	fetchResponses() {
		if(this.locals.fetchInProgress === true) {
			return;
		}
		this.locals.fetchInProgress = true;

		const self = this;
		vwmApi.fetchResponses().then(function(responses){
			self.onResponsesFetched(null, responses);
		},function(error){
			self.onResponsesFetched(error, null);
		});
	}
	newQuestion() {
		this.locals.fetchInProgress = true;

		const self = this;
		vwmApi.newQuestion().then(function(){
			self.onNewQuestion(null);
		}, function(error){
			self.onNewQuestion(error);
		});
	}

	onResponsesFetched(error, responses) {
		this.locals.fetchInProgress = false;
		if(error) {
			this.setState({error:error, errorDescription:'An error occured while fetching the responses. Check the console for more info'});
			console.error('Error while fetching responses: ', error);
		}
		else {
			this.setState({error:null, errorDescription:null})
		}

		this.setState({responses:responses});
		window.clearTimeout(this.locals.fetchTimer);
		this.locals.fetchTimer = window.setTimeout(this.fetchResponses, this.locals.fetchDelay);
	}
	onNewQuestion(error) {
		this.locals.fetchInProgress = false;
		let modifiedFetchDelay = this.locals.fetchDelay;
		if(error) {
			this.setState({error:error, errorDescription:'An error occured while requesting a new question. Check the console for more info'});
			console.error('Error while requesting new question: ', error);
			modifiedFetchDelay *= 2.5;
		}
		else {
			this.setState({error:null, errorDescription:null});

		}

		this.setState({responses:null});
		window.clearTimeout(this.locals.fetchTimer);
		this.locals.fetchTimer = window.setTimeout(this.fetchResponses, modifiedFetchDelay);
	}

	onTypeChosen(type) {
		this.setState({chartType:type});
	}



}

export default Chart;



