import PropTypes from 'prop-types';
import React from 'react';
import ChartJs from 'chart.js';
import chartConfig from './chartConfig';
import generateId from '../../../util/generateId';
import mapsEqual from '../../../util/mapsEqual';
import './ChartCanvas.css'


class ChartCanvas extends React.Component {
	constructor(properties) {
		super(properties);
		this.locals = {
			canvasElement:null,
			canvasId:generateId(6),
			chartJsObj: null,
			'lastResponseCounts': null
		};
	}

	componentDidMount() {
		this.locals.canvasElement = document.getElementById(this.locals.canvasId);
		this.renderChart();
	}
	componentWillReceiveProps(nextProps) {
		this.renderChart(nextProps.responseCounts);
	}
	shouldComponentUpdate(nextProps, nextState) {
		return true;
	}

	render() {
		return <div className='ChartCanvas-wrapper'><canvas className='ChartCanvas' id={this.locals.canvasId}/></div>
	}

	renderChart(responseCountsArg, reset=false) {
		const chartType = this.props.chartType || 'pie';
		let canvasElement = this.locals.canvasElement;
		let responseCounts = responseCountsArg || this.props.responseCounts;
		let newChartType = false;

		if(this.locals.chartJsObj && this.locals.chartJsObj.config.type !== chartType) {
			newChartType = true;
			if(responseCounts == null || responseCounts.length === 0) {
				responseCounts = this.locals.lastResponseCounts;
			}
		}
		else if(responseCounts == null || responseCounts.size === 0) {
			return;
		}
		else if(this.locals.lastResponseCounts && mapsEqual(this.locals.lastResponseCounts, responseCounts)) {
			return;
		}
		this.locals.lastResponseCounts = responseCounts;


		const defaultChartConfig = chartConfig(chartType);
		const chartOptions = defaultChartConfig.options;
		const data = defaultChartConfig.data;
		const dataset = data.datasets[0];


		responseCounts.forEach(function(count, key){
			dataset.data.push(count);
			data.labels.push(key);
		});

		if(this.locals.chartJsObj === null || newChartType === true) {
			canvasElement = this.resetCanvas();

			this.locals.chartJsObj = new ChartJs(canvasElement, {
				type: chartType,
				options: chartOptions,
				data: data
			});
		}
		else {
			// const oldData = this.locals.chartJsObj.config.data;
			// const oldDataset = oldData.datasets[0];
			// dataset.data.forEach(function(datum, index){
			// 	const corispondingLabel = data.labels[index];
			// 	const oldDataLabelIndex = oldData.labels.indexOf(corispondingLabel);
			// 	if(oldDataLabelIndex === -1) {
			// 		this.locals.chartJsObj.data.labels.push(corispondingLabel);
			// 		this.locals.chartJsObj.data.datasets[0].data.push(datum);
			// 	}
			// 	else {
			// 		const oldDataCorispondingDatum = oldDataset.data[oldDataLabelIndex];
			// 		if(oldDataCorispondingDatum !== datum) {
			// 			this.locals.chartJsObj.data.datsets[0].data.splice(oldDataLabelIndex, 1, datum);
			// 		}
			//
			// 	}
			// }, this);

			this.locals.chartJsObj.config.data = data;
			this.locals.chartJsObj.update();
		}
	}

	resetCanvas() {
		if(this.locals.canvasElement === null) {return};
	 	const newCanvas = resetElement(this.locals.canvasElement);
		this.locals.canvasElement = newCanvas;

		return newCanvas;
	}
}

function resetElement(element) {
	let clone = element.cloneNode(true);
	element.parentElement.replaceChild(clone, element);

	return clone;
}

ChartCanvas.propTypes = {
	responseCounts: PropTypes.instanceOf(Map),
	chartType: PropTypes.string
}

export default ChartCanvas;



