import 'whatwg-fetch';
import sample from './sampleResponse.json';

function isNullOrEmptyString(value) {
	if(value == null) {
		return true;
	}
	else if(typeof value === 'string' && value.trim().length === 0) {
		return true;
	}
	return false;
}

const vwmApi = new function() {
	const sessionId = 12345;
	const url = `https://vkavxqwse1.execute-api.us-east-1.amazonaws.com/dev/question/data/${sessionId}`
	const nextUrl = url + '/next';

	this.fetchResponses = function fetchResponses(useSample) {
		if(useSample) {
			return new Promise(function(resolve, reject){
				setTimeout(function(){
					resolve(sample);
				}, 500);

			});
		}

		return fetch(url, {method:'GET'})
		.then(function(response){
			if(response.ok === false) {
				throw new Error(`Fetch GET request responded with status code ${response.status}, Status text: ${response.statusText}`);
			}
			else {
				return response.json();
			}
		});
	}

	this.newQuestion = function newQuestion(useSample) {
		if(useSample) {
			return new Promise(function(resolve, reject){
				setTimeout(function(){
					resolve(sample);
				}, 500);

			});
		}

		return fetch(nextUrl, {method: 'POST'})
		.then(function(response){
			if(response.ok === false) {
				throw new Error(`Fetch POST request responded with status code ${response.status}, Status text: ${response.statusText}`);
			}
			else {
				return null;
			}
		});
	}
	this.getResponseCounts = function getResponseCounts(responses) {
		let responseCountMap = new Map();
		responses.forEach(function(response){
			if(isNullOrEmptyString(response.Body)) {
				return;
			}
			const body = response.Body.toLowerCase().trim();
			const count = responseCountMap.get(body) || 0;
			responseCountMap.set(body, count + 1);
		});

		return responseCountMap;
	}
}();

export default vwmApi;



