import React from 'react';
import PropTypes from 'prop-types';
import './ChartType.css'
import pieChartIcon from './pie-chart.svg';
import doughnutChartIcon from './doughnut-chart.svg';
import polarChartIcon from './polar-chart.svg'
import barChartIcon from './bar-chart.svg';

class ChartType extends React.Component {
	constructor(properties) {
		super(properties);
		this.onBtnClicked = this.onBtnClicked.bind(this);
	}

	render() {
		function backgroundStyle(svg) {
			return {backgroundImage: `url(${svg})`}
		}
		return (
			<div className='ChartType'>
				<button onClick={this.onBtnClicked} className='ChartType-btn' data-chart-type='pie' style={backgroundStyle(pieChartIcon)}/>

				<button onClick={this.onBtnClicked} className='ChartType-btn' data-chart-type='doughnut' style={backgroundStyle(doughnutChartIcon)}/>

				<button onClick={this.onBtnClicked} className='ChartType-btn' data-chart-type='polarArea' style={backgroundStyle(polarChartIcon)}/>

				<button onClick={this.onBtnClicked} className='ChartType-btn' data-chart-type='bar' style={backgroundStyle(barChartIcon)}/>
			</div>
		);
	}

	onBtnClicked(event) {
		let typeAttribute = event.currentTarget.getAttribute('data-chart-type');
		this.props.onTypeChosen(typeAttribute);
	}
}

ChartType.propTypes = {
	onTypeChosen: PropTypes.func
}

export default ChartType;



